import Header from "./Header";
import Footer from "./Footer";
import MainPage from "./MainPage";
import Login from "./Login";
import FormPartOne from "./FormPartOne";
import FormPartTwo from "./FormPartTwo";
import FormPartThree from "./FormPartThree";
import FormPartFour from "./FormPartFour";
import { Route, useRouteMatch } from "react-router-dom";

function App() {
  const { path, url } = useRouteMatch();
  return (
    <>
      <Route path="/">
        <Header />
        <Route exact path={`${path}`}>
          <MainPage />
        </Route>
        <Route path={`${path}/login`}>
          <Login />
        </Route>
        <Route path={`${path}/form-part-one`}>
          <FormPartOne />
        </Route>
        <Route path={`${path}/form-part-two`}>
          <FormPartTwo />
        </Route>
        <Route path={`${path}/form-part-three`}>
          <FormPartThree />
        </Route>
        <Route path={`${path}/form-part-four`}>
          <FormPartFour />
        </Route>
        <Footer />
      </Route>
    </>
  );
}

export default App;
